@import url(https://fonts.googleapis.com/css?family=Righteous);
/* Ref: https://codepen.io/carpenumidium/pen/hHjEJ */

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
}

html,
body {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}
body {
  text-align: center;
  font-family: "Righteous", serif;
  background-color: hsla(230, 40%, 50%, 1);
  color: white;
}
body:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  font-size: 0;
  height: 100%;
}

h1 {
  display: inline-block;
  color: white;
  font-family: "Righteous", serif;
  font-size: 5em;
  text-shadow: 0.03em 0.03em 0 hsla(230, 40%, 50%, 1);
}
h1:after {
  content: attr(data-shadow);
  width: 100%;
  position: absolute;
  top: 0.06em;
  left: 0.06em;
  z-index: -1;
  text-shadow: none;
  background-image: linear-gradient(
    45deg,
    transparent 45%,
    hsla(48, 20%, 90%, 1) 45%,
    hsla(48, 20%, 90%, 1) 55%,
    transparent 0
  );
  background-size: 0.05em 0.05em;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  animation: shad-anim 15s linear infinite;
}

@keyframes shad-anim {
  0% {
    background-position: 0 0;
  }
  0% {
    background-position: 100% -100%;
  }
}

.App {
  text-align: center;
}

.container {
  margin-top: 61.8%;
  display: flex;
  flex-direction: column;
}

main {
  min-height: 100vh;
}

.list-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.footer {
  align-self: flex-end;
}
.list {
  width: 375px;
  padding: 0 32px;
}

.list__tab-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 32px;
}

.list__tab-container .list__tab {
  border-bottom: 2px solid transparent;
  transition: border 300ms ease-out, opacity 300ms ease-out;
}

.list__tab-container .list__tab:not(:last-of-type) {
  margin-right: 48px;
}

.list__tab-container .list__tab:hover {
  cursor: pointer;
  border-bottom: 2px solid white;
  opacity: 0.75;
}
.list__tab-container .list__tab.list__tab--active {
  border-bottom: 2px solid white;
}

.list__items-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.list__items-container__item--empty {
  opacity: 0.75;
}

.list__items-container__item {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  padding: 4px 0;
}

.list__items-container__item a {
  transition: opacity 300ms ease-out;
  color: white;
  text-decoration: none;
}

.list__items-container__item a:hover {
  cursor: pointer;
  opacity: 0.75;
}

.list__items-container__item a:nth-child(2) {
  font-size: 0.8em;
}

.list__items-container__showtime-item {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  padding: 4px 0;
}
